body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
form.number-setting {
  margin: 4px;
  font-size: normal;
}

table {
  border-spacing: 2px;
  background: blue;
}

th, tr, td {
  font-size: medium;
  text-decoration: none;
  padding-top: 2px;
  padding-bottom: 2px;
  padding-left: 4px;
  padding-right: 4px;
  color: blue;
  background: white;
}

th.head, td.head {
  font-weight: bold;
  min-width: 30px;
}

td.head {
  min-width: 60px;
}

th.empty, td.empty {
  background: blue;
}

th.normal, td.normal {
  background: white;
}

th.checked, td.checked {
  text-decoration: line-through;
  background: gray;
}

th.expected, td.expected {
  background: green;
}

td.cell {
  padding: 0px;
}

select {
  font-size: small;
}

select.numer-of-players, select.numer-of-items, select.number-of-cards-per-player {
  width: 50px;
}

select.cell {
  padding: 0px;
  width: 100%;
  height: 100%;
}
